import React, { Component } from "react";
import "../CSS/Header.css";
export class Header extends Component {
  render() {
    return (
      <>
        <div className="banner">
          <div className="row ">
            <div className="col-2  text-center align-items-center d-flex justify-content-center">
              <img
                src="https://emachat.com/chat/ill/logo.php?c=white"
                width="200"
                alt=""
              />
            </div>
            <div className="col-10  align-items-center d-flex">
              <h1 className="display-5">
                EmaChat Recruitments &#9432;
                <div className="fs-6">
                  Lifestyle • Strasbourg, Alsace, France • 100k followers
                </div>
              </h1>
              <br />
            </div>
          </div>
        </div>
      </>
    );
  }
}

export default Header;
