import React, { Component } from 'react'
import IndexContent from '../IndexContent/IndexContent'
import Header from '../Header/Header'
import SecondaryContent from '../IndexContent/SecondaryContent'
import TertiaryContent from '../IndexContent/TertiaryContent'
import FinalContent from '../IndexContent/FinalContent'

export class Pages extends Component {
    render() {
        return (
            <>
                <Header/>
                <IndexContent/>
                <SecondaryContent/>
                <TertiaryContent/>
                <FinalContent/>
            </>
        )
    }
}

export default Pages
